#search-wrapper {
  padding: 40px 0;

  display: flex;
  justify-content: center;

  background-color: rgba(0, 0, 0, 0.05);
}

.search-container {
  width: 1200px;

  display: flex;
  flex-direction: column;

  gap: 20px;
}

.search-title-box {
  display: flex;
  gap: 8px;
}

.search-title {
  color: rgba(0, 0, 0, 0.7);

  font-size: 24px;
  font-weight: 400;
  line-height: 140%;
}

.search-title-emphasis {
  color: rgba(0, 0, 0, 1);

  font-weight: 600;
}

.search-count {
  color: rgba(0, 0, 0, 1);

  font-size: 24px;
  font-weight: 600;
  line-height: 140%;
}

.search-contents-box {
  display: grid;
  grid-template-columns: 8fr 4fr;
  gap: 24px;
}
.search-contents-result-box {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.search-contents-result-nothing {
  height: 416px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: rgba(0, 0, 0, 0.4);

  font-size: 24px;
  font-weight: 600;
  line-height: 140%;
}

.search-relation-word-card {
  padding: 24px;

  background-color: rgba(255, 255, 255, 1);
}

.search-relation-card-box {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.search-relation-card-title {
  color: rgba(0, 0, 0, 1);

  font-size: 24px;
  font-weight: 600;
  line-height: 140%;
}

.search-relation-card-contents{
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    
}

.search-relation-card-contents-nothing {
  height: 252px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: rgba(0, 0, 0, 0.4);

  font-size: 24px;
  font-weight: 600;
  line-height: 140%;
}

.search-pagination-box {
  margin-top: 60px;

  display: flex;
  justify-content: center;
}
