.top3-list-item-card {
  flex: 1;

  border-radius: 10px 10px;
  padding: 24px;
  height: 460px;

  display: flex;
  flex-direction: column-reverse;

  background-color: rgba(0, 0, 0, 0.3);
  background-position: 50% 50%;
  background-size: 100% 100%;
}

.top3-list-item-card:hover {
  cursor: pointer;
  opacity: 0.8;
}

.top3-list-item-main-box {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.top3-list-item-top {
  display: flex;
  align-items: center;
  gap: 8px;
}

.top3-list-item-profile-box {
  width: 32px;
  height: 32px;
}

.top3-list-item-write-box {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.top3-list-item-nickname {
    color: rgba(255, 255, 255, 1);

    font-size: 12px;
    font-weight: 500;
    line-height: 140%;
}

.top3-list-item-write-date{
    color: rgba(255, 255, 255, 0.7);

    font-size: 12px;
    font-weight: 400;
    line-height: 140%;
}

.top3-list-item-profile-image {
  border-radius: 50% 50%;
  width: 100%;
  height: 100%;

  background-position: 100%;
  background-size: 100%;
}

.top3-list-item-middle {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.top3-list-item-title {
  color: rgba(255, 255, 255, 1);

  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
}

.top3-list-item-contents {
  color: rgba(255, 255, 255, 0.7);

  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}

.top3-list-item-counts {
  color: rgba(255, 255, 255, 0.7);

  font-size: 12px;
  font-weight: 400;
  line-height: 140%;
}
